




















export default {
	data() {
		return {
			list:[],
			icon:[],
		}
	},
	onLoad() {
	},
	onShow() {
		this.icon = this.common.get("icon")
		// console.log('this.icon',this.icon)
		let uid = this.common.get("uid")
		if (uid != "") {
			this.listInterface()
		} else {
			uni.switchTab({
			     url: "/pages/user/user/user"
			})
		}
	},
	methods: {
		listInterface() {
			// 消息接口
			this.http({
				url:this.api.getLogList,
				method: "POST",
				data: {
					uid: this.common.get("uid"),
					token: this.common.get("token")
				},
				success: res => {
					if(res.code == 0) {
						uni.switchTab({
						     url: "/pages/user/user/user"
						})
					} else {
						if(res.list.length == 0) {
							uni.showToast({
								title:'无数据',
								icon:'none',
								duration: 2000
							})
						}
						this.list = res.list
					}
				}
			})
		},
		// 全部已读点击
		yidu() {
			this.http({
				url:this.api.setLogReaded,
				method: "POST",
				data: {
					uid: this.common.get("uid"),
					token: this.common.get("token")
				},
				success: res => {
					this.listInterface()
				}
			})
		},
		// 详情
		detail(eve) {
			uni.navigateTo({
				url:'/pages/index/details/details?data='+encodeURIComponent(JSON.stringify(eve))
			})
		},
	}
}
